import './styles.css';
import fetchCountries from './fetchCountries';
import country from './templates/country.hbs';
import { error } from '@pnotify/core';
import '@pnotify/core/dist/BrightTheme.css';
import debounce from 'lodash.debounce';

const countryInpup = document.getElementById('country');
const result = document.getElementById('result');
const card = document.querySelector('.card');

countryInpup.addEventListener(
  'input',
  debounce(() => {
    fetchCountries(countryInpup.value).then(data => {
      if (data.length === 1) {
        result.innerHTML = '';
        card.innerHTML = country(data[0]);
      } else if (data.length > 1 && data.length <= 10) {
        card.innerHTML = '';
        updateListMarkup(data);
      } else if (data.length > 10) {
        card.innerHTML = '';
        result.innerHTML = '';
        error({
          sticker: false,
          closer: false,
          text: 'Too many matches found. Please enter a more specific query!',
          maxTextHeight: null,
          delay: 400,
        });
      }
    });
  }, 500),
);

function updateListMarkup(arr) {
  let markup = '';
  arr.forEach(item => {
    markup += `<li>${item.name}</li>`;
    result.innerHTML = markup;
  });
}
